html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}
html {
  font-size: 20px;
  font-weight: 300;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  line-height: 1.5;
}
body {
  min-height: 100vh;
  background-color: #333;
  color: #eee;
}
header,
main,
footer {
  width: 80vw;
  padding: 1rem 2rem;
  margin: 0 auto;
}
header h1 {
  color: orange;
}
header h2 {
  color: orangered;
}
p {
  line-height: 1.7;
  margin: 1.5rem 0;
}
img {
  max-width: 50vw;
}
